/*=================================================================================
	Menu Design
=================================================================================*/
.menu {
  position: absolute;
  background: #FFF;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  z-index: 199; }

.show-menu .menu {
  opacity: 1;
  visibility: visible; }

.menu-items {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
  -webkit-transform: translate3d(0, -50%, 0);
  transform: translate3d(0, -50%, 0);
  z-index: 99; }
  .menu-items > ul {
    list-style: none;
    padding: 0;
    margin: 0; }
    .menu-items > ul > li {
      display: inline-block;
      *display: inline;
      *zoom: 1;
      margin: 28px;
      position: relative; }
      .menu-items > ul > li > button {
        display: block;
        text-decoration: none;
        color: #202020;
        opacity: 0;
        -webkit-transition: all .3s ease;
        transition: all .3s ease;
        -webkit-transform: translateY(15px);
        transform: translateY(15px); }

.show-menu .menu-items > ul > li > button {
  opacity: 1;
  border-style: inherit;
  -webkit-transform: translateY(0px);
  transform: translateY(0px); }

.menu-items > ul > li > button > i {
  display: block;
  height: 130px;
  width: 130px;
  line-height: 130px;
  font-size: 58px;
  vertical-align: middle;
  background: #F0F0F0;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  border-radius: 50%; }
  .menu-items > ul > li > button > i:before {
    line-height: 130px;
    vertical-align: middle; }

.menu-items > ul > li > button > span {
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 14px;
  margin-top: 12px;
  display: block;
  color: #B6B6B6;
  position: relative;
  z-index: 9;
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }

.menu-items > ul > li > button:hover > i {
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.18);
  background: #F44336;
  color: #FFF; }

.menu-items > ul > li > button:hover > span {
  color: #202020; }

.show-menu .menu-items > ul > li:nth-of-type(1) > button {
  -webkit-transition-delay: .4s;
  transition-delay: .4s; }

.show-menu .menu-items > ul > li:nth-of-type(2) > button {
  -webkit-transition-delay: .5s;
  transition-delay: .5s; }

.show-menu .menu-items > ul > li:nth-of-type(3) > button {
  -webkit-transition-delay: .6s;
  transition-delay: .6s; }

.show-menu .menu-items > ul > li:nth-of-type(4) > button {
  -webkit-transition-delay: .7s;
  transition-delay: .7s; }

.show-menu .menu-items > ul > li:nth-of-type(5) > button {
  -webkit-transition-delay: .8s;
  transition-delay: .8s; }

.show-menu .menu-items > ul > li:nth-of-type(6) > button {
  -webkit-transition-delay: .9s;
  transition-delay: .9s; }

.show-menu .menu-items > ul > li:nth-of-type(7) > button {
  -webkit-transition-delay: 1s;
  transition-delay: 1s; }

.sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #F0F0F0;
  padding: 25px 10px;
  list-style: none;
  margin: 0;
  margin-top: -88px;
  border-radius: 0 0 50% 50%;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }

.menu-items > ul > li:hover > .sub-menu {
  opacity: 1;
  visibility: visible; }

.sub-menu > li > a {
  display: block;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #808080;
  text-decoration: none;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  font-size: 12px;
  padding: 2px 0; }
  .sub-menu > li > a:hover {
    color: #202020; }

/*=================================================================================
	Responsive CSS
=================================================================================*/
@media (max-width: 600px) {
  .menu-items {
    -webkit-transform: none;
    transform: none;
    position: static; } }

/*=================================================================================
	Menu Button
=================================================================================*/
.menu-btn {
  position: absolute;
  height: 64px;
  width: 64px;
  line-height: 64px;
  border-radius: 50%;
  border-style: none;
  background: #009688;
  box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.18);
  top: 50%;
  margin-top: -32px;
  left: -32px;
  display: block;
  overflow: hidden;
  text-align: center;
  z-index: 999;
  -webkit-transition: all .3s ease;
  transition: all .3s ease; }
  .menu-btn:hover {
    box-shadow: 0 10px 35px 0 rgba(0, 0, 0, 0.18); }
  .menu-btn > .lines {
    vertical-align: middle;
    display: inline-block;
    *display: inline;
    *zoom: 1;
    height: 32px;
    width: 32px;
    position: relative; }
    .menu-btn > .lines > span {
      position: absolute;
      left: 0;
      display: block;
      height: 4px;
      border-radius: 2px;
      width: 32px;
      margin: auto;
      background: #FFF;
      -webkit-transition: all .3s ease;
      transition: all .3s ease; }
    .menu-btn > .lines > .l1 {
      top: 5px; }
    .menu-btn > .lines > .l2 {
      top: 14px; }
    .menu-btn > .lines > .l3 {
      top: 23px; }

.show-menu .menu-btn > .lines > .l2 {
  opacity: 0; }

.show-menu .menu-btn > .lines > .l1, .show-menu .menu-btn > .lines > .l3 {
  top: 50%;
  margin-top: -2px; }

.show-menu .menu-btn > .lines > .l1 {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

.show-menu .menu-btn > .lines > .l3 {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg); }

/*=================================================================================
	Responsive CSS
=================================================================================*/
@media (max-width: 600px) {
  .menu-btn {
    right: 30px;
    bottom: 30px;
    top: auto;
    left: auto;
    -webkit-transform: none;
    transform: none; } }

